











































	import messageShow from '../../../components/messagesshow.vue';
	import resultMessage from '../../../components/resultmessage.vue'
	import {
		mapState,
		mapMutations,
		mapGetters
	} from 'vuex'
	export default {

		data() {
			return {
				style: {
					pageHeight: 0,
					contentViewHeight: 0,
					footViewHeight: 90,
					mitemHeight: 0,
					bannerShow: false,
				},
				bannerShow: false,
				scrollTop: 0,
				grabhb: false,
				messages: [],
				roomid: '',
				callindex: '',
				currentHb_id:'',
				grabacount:''
			};
		},

		onLoad(e) {
			console.log(e);
			uni.setNavigationBarTitle({
				title: e.title
			});

			var websocketIsOpen = e.websocketIsOpen;
			const res = uni.getSystemInfoSync();
			this.style.pageHeight = res.windowHeight;
			this.style.contentViewHeight = res.windowHeight - uni.getSystemInfoSync().screenWidth / 750 * (100); //像素
			this.roomid = e.roomid;
			this.callindex = e.callindex;
			this.loadRoomInfo(this.roomid, this.callindex, websocketIsOpen);
		},
        
		computed: {
			...mapGetters({
				userid: 'getUserId',
				user:'getUserInfo'
			}),
		},

		onBackPress: function(res) {
			var socketpra = {
				user_id: this.userid,
				room_id: this.roomid,
				act: 'quit',
				msg: ''
			}
			var msg = JSON.stringify(socketpra);
			console.log('msg' + msg);
			uni.sendSocketMessage({
				data: msg
			});
		},
		methods: {
			closeBanner: function() {
				this.bannerShow = !this.bannerShow;
			},
			closegrabhb: function() {
				this.grabhb = !this.grabhb;
			},
			loadRoomInfo(roomid, callindex, websocketIsOpen) {
				var that = this;
				var data = {
					roomid: roomid
				}
				var socketpra = {
					user_id: that.userid,
					room_id: roomid,
					act: 'login',
					msg: ''
				}
				var msg = JSON.stringify(socketpra);
				var that = this;
				this.$utils.pluginRequest("pl_room_join", callindex, data, 'POST', '',
					res => {
						//suc
						if (res.code == 0) {
							if (websocketIsOpen) {
								uni.sendSocketMessage({
									data: msg
								});
								uni.onSocketMessage(function(res) {
									console.log(res.data)
									JSON.parse(res.data).data.forEach(function(item, index) {
										item.act = JSON.parse(res.data).act;
										if (item.user_id == that.$store.getters.getUserId) {
											item.role = 'home';
										} else {
											item.role = 'customer';
										}
										item.role = item.user_id == that.$store.getters.getUserId ? 'home' : 'customer'
										that.addMessage(item, that);
										that.scrollToBottom();
									});
								});
							}
						}
					},
					res => {
						//fail
					},
					res => {
						//complete
					}
				)
			},
			addMessage(message, that) {
				var messages = that.messages;
				messages.push(message);
				that.messages = messages;
			},
			scrollToBottom:function(){
// 				var that = this;
// 				var query = uni.createSelectorQuery();
// 				query.selectAll('.m-item').boundingClientRect();
// 				query.select('#scrollview').boundingClientRect();
// 
// 				query.exec(function(res) {
// 					that.style.mitemHeight = 0;
// 					res[0].forEach(function(rect) {
// 						// console.info(rect.height);
// 						that.style.mitemHeight = that.style.mitemHeight + rect.height + 20;
// 					});
// 
// 					if (that.style.mitemHeight > that.style.contentViewHeight) {
// 						that.scrollTop = that.style.mitemHeight - that.style.contentViewHeight;
// 					}
// 				});
			},
			SendRedEnvelope: function(e) {

				uni.navigateTo({
					url: '../sendRnvelope/sendRnvelope?roomid=' + this.roomid + '&callindex=' + this.callindex,
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			balance: function(e) {
				uni.showModal({
					confirmColor: "#FC4B2D",
					confirmText: "知道了",
					showCancel: false,
					content:'余额'+this.user.amount+"元"
				})
			},
			click: function(message) {
				var that = this;
				var data = {
					roomid: this.roomid,
					hbid: message.hb_id
				}
				that.currentHb_id = message.hb_id;
				this.$utils.defalsePluginRequest('pl_hb_open', this.callindex, data,
					res => {
						if (res.code == 0) {
							//抢到红包
							this.grabacount = res.data;
							that.grabhb = true;
							
						} else {
							if (res.code == 3001) {
								//余额不足，不能充值，去充值
								uni.showModal({
									title: '提示',
									content: res.msg,
									showCancel: true,
									cancelText: '知道了',
									confirmText: '去充值',
									confirmColor: '#D81E06',
									success: res => {
										if (res.confirm) {
											//跳转到充值页面
											uni.navigateTo({
												url: '../../my/recharge/recharge',
												success: res => {},
												fail: () => {},
												complete: () => {}
											});
										} else if (res.cancel) {
											
										}
										
									},
									fail: () => {},
									complete: () => {}
								});
							} else {
								//红包已经都抢完了
								
								that.bannerShow = true;
							}


						}
					},
					res => {

					},
					res => {

					}
				)



			},
			clickPic: function() {
				var that = this;
				that.bannerShow = false;
				uni.navigateTo({
					url: '../rayRecords/rayRecords?roomid=' + this.roomid + '&callindex=' + this.callindex+'&hbid='+this.currentHb_id,
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		},
		components: {
			messageShow,
			resultMessage
		}

	}
